<template>
  <v-container
    class="testimonios__container p-0 m-0"
    fluid
  >
    <v-container>
      <h1>Testimonios Pacientes</h1>

      <div class="content">
        <card-testimonios
          v-for="(testimonio, index) in getTestimoniosPaginados"
          :key="index"
          :value="{
            Comentario: testimonio.Comentario,
            Autor: testimonio.Autor,
            Descripcion: testimonio.Descripcion,
          }"
        />
      </div>

      <div class="buttons">
        <v-btn
          :disabled="isDisabledBack"
          color="success"
          small
          @click="page--"
        >
          <v-icon>mdi-chevron-left</v-icon>
        </v-btn>

        <v-btn
          small
          :class="'grey darken-1'"
          @click="autoRunState = !autoRunState"
        >
          <v-icon color="white">
            {{ btnIcon }}
          </v-icon>
        </v-btn>

        <v-btn
          :disabled="isDisabledForward"
          color="success"
          small
          @click="page++"
        >
          <v-icon>mdi-chevron-right</v-icon>
        </v-btn>
      </div>
    </v-container>
  </v-container>
</template>

<script>
  import { mapState } from 'vuex'

  export default {
    name: 'Testimonios',
    components: {
      CardTestimonios: () => import('@/components/web/base/CardTestimonios'),
    },

    data () {
      return {
        page: 1,
        itemsPerPage: 2,
        backBtnStatus: false,
        forwardBtnStatus: false,
        autoRunState: true,
        btnIcon: 'mdi-pause',
      }
    },
    computed: {
      ...mapState('web', ['testimonios']),
      isDisabledBack () {
        if (this.page === 1) {
          return true
        }
        return false
      },
      isDisabledForward () {
        if (this.page === this.getTotalPaginas) {
          return true
        }
        return false
      },
      getTotalPaginas () {
        return Math.ceil(this.testimonios.length / this.itemsPerPage)
      },

      getTestimoniosPaginados () {
        const start = (this.page - 1) * this.itemsPerPage
        const stop = this.page * this.itemsPerPage
        return this.testimonios.slice(start, stop)
      },
    },
    watch: {
      autoRunState: function () {
        if (this.autoRunState === false) {
          this.btnIcon = 'mdi-play'
        } else {
          this.btnIcon = 'mdi-pause'
        }
      },
    },
    created () {
      this.autoRunPage()
    // this.printStatus()
    },
    methods: {
      printStatus () {
        console.log(this.forwardBtnStatus)
        console.log(this.page)
        console.log(this.getTotalPaginas)
        console.log(this.getTestimoniosPaginados)
        console.log(this.testimonios)
      },
      autoRunPage () {
        setInterval(() => {
          if (this.page < this.getTotalPaginas && this.autoRunState === true) {
            this.page++
          } else if (this.autoRunState === true) {
            this.page = 1
          }
        }, 9000)
      },
    },
  }
</script>
